import React, { useState } from 'react';
import Section from '../Section';

interface ServiceProps {
  data: any[];
}

interface ServiceItemProps {
  className: string;
  image: string;
  title: string;
  color: string;
  description: string;
}

const Service: React.FC<ServiceProps> = props => {
  const ServiceItem = (props: ServiceItemProps) => (
    <div
      className={`mb-5 p-4 rounded ${props.className}`}
      style={{
        borderStyle: 'solid',
        borderWidth: '2px',
        borderColor: '#e9e9e9',
        boxSizing: 'border-box',
        letterSpacing: '1.4px',
        lineHeight: '1.8rem'
      }}
    >
      <div
        className='row'
        style={{
          minHeight: '120px'
        }}
      >
        <div className='col-9'>
          <h3 className='h6 mb-4 text-white'>
            <span
              className='p-1'
              style={{
                backgroundColor: props.color,
                lineHeight: '2rem'
              }}
            >
              {props.title}
            </span>
          </h3>
          <p>{props.description}</p>
        </div>
        <div className='col-3 d-flex align-items-center'>
          <img
            className='d-block mx-auto my-0'
            src={props.image}
            alt={props.title}
            width={80}
            style={{
              maxWidth: '80px',
              maxHeight: '80px',
              pointerEvents: 'none'
            }}
          />
        </div>
      </div>
    </div>
  );

  return (
    <Section id='service'>
      <div className='service-background'>
        <div className='row'>
          {props.data.map((d: any, i: number) => (
            <div key={i} className='col-lg-6'>
              <ServiceItem
                className={`service-item-${i + 1}`}
                image={d.image.url}
                title={d.title}
                color={d.color}
                description={d.description}
              />
            </div>
          ))}
        </div>
      </div>
    </Section>
  );
};

export default Service;
